import React from 'react'
import {useState,useEffect} from 'react'
import {BonusIcon,DashboardIcon,CopyCom,IncomeIcon,ReferralIcon} from '../common'
import {injectIntl,FormattedMessage,Link} from 'gatsby-plugin-intl'
import Skeleton from '../skeleton'
import Cookies from 'js-cookie'
import Web3 from 'web3'
import config from '../../../config/default.json'
import defaultVars from '../../libs/config'
import contractAbi from '../../libs/abi'
import {renderDays} from '../../libs/day'
import {BounceLoader} from 'react-spinners'
import { toast } from 'react-toastify'
import BigNumber from 'bignumber.js'
import {connect} from 'react-redux'
import {navigate} from 'gatsby'
import _ from 'lodash'
const web3 = new Web3(defaultVars.provider);
const contract = new web3.eth.Contract(contractAbi,defaultVars.contractAddress);
const prices = defaultVars.prices;
const refs = defaultVars.refs;
const incomes = defaultVars.incomes;
const bonusRewards = defaultVars.bonus;
const BonusItems = config.bonusItems;
const BlockBonusItem = ({l,i,r,c,ch,p,userInfo}) => {
    return (
        <div className="w-full md:w-1/5 md:px-2 mb-4 md:mb-0" key={i}>
            <div className={`text-white text-xl uppercase font-nexa text-center px-8 py-8 bg-${c}`}>
                <div className="mb-0 leading-none">
                    <FormattedMessage id="index.bonus.rank" defaultMessage="Rank"/>
                </div>
                <div className="leading-none">{l}</div>
            </div>
            <div className="bg-white px-8 py-8 text-center md:px-4 md:py-4 lg:px-8 lg:py-8">
                    <div className="font-sans font-semibold text-13 text-lights-gray">
                        <FormattedMessage id="index.bonus.num" defaultMessage="ID Number" />
                    </div>
                <div className="font-sans font-semibold text-18 text-lights-bluebold mb-8 md:mb-4 lg:mb-8">{i}</div>
                <div className="inline-block text-center relative">
                    <svg className="w-24 h-24 mx-auto"viewBox="0 0 400 400" style={{transform: 'rotate(-90deg)'}}>
                        <circle cx="200" cy="200" r="180" fill="none" strokeWidth="20" stroke="#e1e1e1"></circle>
                        <circle cx="200" cy="200" r="180" fill="none" strokeWidth="20" stroke={`${ch}`} strokeDasharray={Math.PI*180*2} strokeDashoffset={((100-(userInfo && userInfo.userCounts ?(_.min(userInfo.userCounts) > i ? 100 : ((_.min(userInfo.userCounts)/i)*100)):0))/100)*(Math.PI*180*2)} strokeLinecap="round"></circle>
                    </svg>
                    <div className="absolute bonus-circle-text">
                        <div className="font-sans font-semibold text-13 text-lights-gray">
                            <FormattedMessage id="index.bonus.award" defaultMessage="Award" />
                        </div>
                        <div className={`font-sans font-extrabold text-18 leading-none text-${c}`}>{r}</div>
                    </div>
                </div>
            </div>
        </div>        
    )
}
const mapStateToProps = ({userInfo,lv,lvs,total,rank,totalBonus}) => {
    return {userInfo,lv,lvs,total,rank,totalBonus}
}

const Db = () => {
    const [loading,setLoading] = useState(true);
    const [userInfo,setUserInfo] = useState(false);
    const [lv,setLv] = useState(null);
    const [lvs,setLvs] = useState(null);
    const [total,setTotal] = useState(null);
    const [rank,setRank] = useState(null);
    const [totalBonus,setTotalBonus] = useState(null);
    const [showBox,setShowBox] = useState(false);
    const [buyLv,setBuyLv] = useState(false);
    useEffect(()=>{
        const address = Cookies.get('user');
        const a = async()=>{
            const user = await contract.methods.us(address).call();
            const userRefs = await contract.methods.vUR(address).call();
            let userCounts = [0,0,0];
            for (let i = 0;i < userRefs.length; i++){
                let result = await contract.methods.vUT(address,userRefs[i].toString()).call();
                console.log(result)
                userCounts[i] = Number(result);
            }
            setUserInfo({address,id:user.a.toString(),userCounts})
        }
        const b = async()=>{            
            const lv5 = contract.methods.vULE(address,5).call();
            
            const lv4 = contract.methods.vULE(address,4).call();
            
            const lv3 = contract.methods.vULE(address,3).call();
            
            const lv2 = contract.methods.vULE(address,2).call();
            
            const lv1 = contract.methods.vULE(address,1).call();
            const rs = await Promise.all([lv1,lv2,lv3,lv4,lv5]);
            let c = 1;
            for (let [i,v] of rs.entries()){
                rs[i] = v.toString();
                if (Number(v.toString()) > 0){
                c = i + 1;
                }
            }
            setLvs(rs);
            setLv(c);
        }
        const c = async()=>{
            const event = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:["0x07c3121cf5ba6f12de77662eba4264c329ab040b9912576221c15ee6f097d96a",`0x000000000000000000000000${address.slice(2,)}`]});
            let amount = 0;
            for (let i of event){
                amount += prices[i.returnValues._l.toString() - 1];
            }
            setTotal(amount);
        }
        const dr = async() => {
            const r1 = contract.methods.vURS(address,0).call();
            const r2 = contract.methods.vURS(address,1).call();
            const r3 = contract.methods.vURS(address,2).call();
            const r4 = contract.methods.vURS(address,3).call();
            const r5 = contract.methods.vURS(address,4).call();
            const rs = await Promise.all([r1,r2,r3,r4,r5]);
            let r = 0;
            for (let val of rs){
                if (!val){
                    break;
                }
                r++;
            }
            setRank(r);
        }
        const e = async() => {
            const event = await contract.getPastEvents("allEvents",{fromBlock: 0,topics:["0x154bd3f0184bafa2f1300062924f0db714a9bf2cc5ff17ee8fbf5bfa9471ee16",`0x000000000000000000000000${address.slice(2,)}`]})
            let amount = 0;
            for (let i of event){
                amount += bonusRewards[i.returnValues._l.toString() - 1];
            }
            setTotalBonus(amount);
        }
        a();
        b();
        dr();
        c();
        e();
    },[null])
    const handleLevelUp = async () => {
        if (lv && lv <=4){
            setShowBox({title: 'Buy Level',level: lv+1})
        } else {
            toast.error('You are max level.')
        }
    }
    const handleBuyLevel = async() => {
        if (window.ethereum && !buyLv){
            setBuyLv(true);
            window.web4 = new Web3(window.ethereum,null,{transactionConfirmationBlocks: 1,transactionBlockTimeout: 60});
            // const netVersion = await window.web4.eth.net.getId();
            // if (netVersion !== defaultVars.net){
            //     toast.error(defaultVars.noti)
            //     setBuyLv(false);
            //     return;
            // }
            try {
                const acc = await window.ethereum.enable();
                console.log(acc);
                console.log(defaultVars.prices[showBox.level-1]);
                const gasPrice = await window.web4.eth.getGasPrice();
                console.log(gasPrice)
                const bigNumberGwei = new BigNumber(gasPrice).plus(defaultVars.gweiPlus).toString();
                let gasCal = await window.web4.eth.estimateGas({from: acc[0],to: defaultVars.contractAddress,value: web3.utils.toWei(`${defaultVars.prices[showBox.level-1]}`,'ether'),data: '0x'});
                const bigNumberGasCal = new BigNumber(gasCal).plus('1');
                console.log(gasCal);
                await window.web4.eth.sendTransaction({from: acc[0],to: defaultVars.contractAddress,value: web3.utils.toWei(`${defaultVars.prices[showBox.level-1]}`,'ether'),gas: bigNumberGasCal,gasPrice: bigNumberGwei,data: '0x'});
                toast.success('Success.')
                setBuyLv(false);
                navigate('/db/dashboard/');
            } catch(error){
                console.log(error)
                setBuyLv(false);
            }
        } else {
            setBuyLv(false);
        }
    }
    const handleClose = () => {
        if (buyLv){
            return toast.error('Please wait for buying success!')
        }
        setShowBox(false);
    }
    return (
        <div className="overflow-hidden">
            {showBox ? <div className="fixed h-screen w-screen top-0 left-0 px-2 py-2 flex items-center justify-center z-50" style={{backgroundColor: 'rgba(0,0,0,.3)'}}>
                <div className="bg-white shadow w-full md:w-2/3 lg:w-1/2 xl:w-1/3 shadow-lg rounded relative">
                    <div className="px-4 py-4 text-14 text-lights-bluebold font-sans font-semibold border-b">{`${showBox.title} ${showBox.level}`}</div>
                    <div className="px-4 py-4">
                        <div className="font-sans text-14 text-lights-bluebold mb-2">Send {prices[showBox.level-1]} ETH to the smart contract address:</div>
                        <div className="mb-2">
                            <CopyCom text={defaultVars.contractAddress}/>
                        </div>
                        <div className="text-13 text-lights-bluebold font-sans text-center mb-4">
                            <div className="">The "Input DATA" field can be unfilled.</div>
                            <div className="">The smart contract will instantly redirect the money to your upline of the appropriate line.</div>
                        </div>
                        <div className="text-center text-14 font-sans font-semibold mb-4">or</div>
                        <div className="">
                            <button className="focus:outline-none py-2 bg-lights-bluebold text-white font-sans text-14 font-semibold rounded flex items-center justify-center mx-auto hover:bg-lights-orangebold" style={{width: '200px'}} onClick={handleBuyLevel}>
                                {buyLv ? <BounceLoader color="#fff" size={21} /> : 'Buy With 1 Click'}
                            </button>
                        </div>
                    </div>
                    <div className="px-4 py-2 border-t">
                        <div className="cursor-pointer px-1 py-1 w-10 h-10 ml-auto" onClick={handleClose}>
                            <img src="/img/cancel.svg" alt="" className="w-8 h-8"/>
                        </div>
                    </div>
                </div>
            </div>:null}
            <div className="md:flex md:-mx-4 mb-4">
                <div className="w-full md:w-1/2 md:px-4 mb-4 md:mb-0">
                    <div className="px-2 py-6 bg-lights-bluebold shadow text-lights-orangebold font-sans font-semibold text-14 flex items-center md:px-6">
                        {totalBonus ===null ? <Skeleton width={40} height={40} circle={true}/> : <BonusIcon size="large"/>}
                        <span className="pl-6">
                            {totalBonus === null ? <Skeleton width={40}/> : `${totalBonus} ETH`}
                        </span>
                    </div>
                </div>
                <div className="w-full md:w-1/2 md:px-4">
                    <div className="px-2 py-6 bg-lights-bluebold shadow text-lights-orangebold font-sans font-semibold text-14 flex items-center md:px-6">
                        {userInfo ? <DashboardIcon size="large"/> : <Skeleton circle={true} width={40} height={40}/>}
                        <span className="pl-6">
                            <div className="text-11 mb-1">
                                {userInfo ? userInfo.address : <Skeleton width={260} />}
                            </div>
                            <div className="">
                                {userInfo ? 'Active member' : <Skeleton width={90}/>}
                            </div>
                        </span>
                    </div>
                </div>
            </div>
            {/* block2 */}
            <div className="md:flex md:-mx-4 mb-4">
                <div className="w-full md:w-1/2 md:px-4 mb-4 md:mb-0">
                    <div className="px-2 py-6 bg-white shadow text-center" style={{minHeight: '280px'}}>
                        {userInfo && rank !== null ? <div className="font-sans font-bold text-16 uppercase text-lights-orangebold mb-2"><FormattedMessage id="db.db.rank" defaultMessage="Current rank"/>: <span className="text-lights-bluebold">{rank}</span></div>: <div className="mb-2"><Skeleton width={170} height={22}/></div>}
                        {userInfo && rank !== null ? <div className="text-lights-gray font-sans text-11 mb-4">
                            <FormattedMessage id="db.db.mem" defaultMessage="Total member"/>: ({userInfo.userCounts[0]}),({userInfo.userCounts[1]}),({userInfo.userCounts[2]})
                        </div>: <div className="mb-1"><Skeleton width={140} height={11}/></div>}
                        {userInfo && rank !== null ? <div className="">
                            <img src="/img/db--01.png" alt="" className="w-full"/>
                        </div>:<div><Skeleton height={163}/></div>}
                    </div>
                </div>
                <div className="w-full md:w-1/2 md:px-4 mb-4 md:mb-0">
                    <div className="px-2 py-6 bg-white shadow text-center">
                        {lv ? <div className="font-sans font-bold text-16 uppercase text-lights-orangebold mb-2"><FormattedMessage id="db.db.level" defaultMessage="Current level"/>: <span className="text-lights-bluebold">{lv} </span>
                            <FormattedMessage id="index.profit.level" defaultMessage="level"/>
                        </div>:<div className="mb-2"><Skeleton width={170} height={22}/></div>}
                        {lv ? <div className="text-lights-gray font-sans text-11 mb-1">
                            <span>{lv+1} </span>
                            <FormattedMessage id="index.profit.level" defaultMessage="level"/>: <FormattedMessage id="index.profit.price" defaultMessage="price"/>
                            <span className="font-semibold text-lights-bluebold"> {prices[lv]} ETH</span>
                        </div>:<div><Skeleton width={120} height={11}/></div>}
                        {lv ? <div className="text-lights-gray font-sans text-11 mb-4">
                            <span>{lv+1} </span>
                            <FormattedMessage id="index.profit.level" defaultMessage="level"/>: <FormattedMessage id="index.profit.income" defaultMessage="income"/>
                            <span className="font-semibold text-lights-bluebold"> {incomes[lv]} ETH</span>
                        </div>:<div><Skeleton width={130} height={11}/></div>}
                        <div className="mb-4">
                            {lv ? <button className="text-11 bg-lights-orangebold text-white inline-block px-3 py-1 rounded font-sans font-semibold focus:outline-none capitalize" onClick={handleLevelUp}>
                                <FormattedMessage id="db.db.lvup" defaultMessage="Level Up"/>
                            </button>:<Skeleton width={70} height={24}/>}
                        </div>
                        <div className="mb-4">
                            <div className="font-sans text-11 text-lights-bluebold capitalize mb-2">
                                {lv && userInfo ? <FormattedMessage id="db.db.link" defaultMessage="Partner Hyperlink"/> : <Skeleton width={90} height={15} />}
                            </div>
                            {lv && userInfo ? <CopyCom text={`${defaultVars.link}/?ref=${userInfo.id}`}/> : <Skeleton height={37}/>}
                        </div>
                        {lv ? <div className="">
                            <Link to="/" className="text-lights-orangebold font-bold text-11 font-sans">
                                <FormattedMessage id="db.db.invite" defaultMessage="Invite"/>                            
                            </Link>
                                <span className="text-11 font-sans text-lights-bluebold font-normal ml-1">
                                    <FormattedMessage id="db.db.refs" defaultMessage="referrals"/>
                                </span>
                        </div> : <Skeleton width={90} height={14} />}
                    </div>
                </div>
            </div>
            {/* block2 */}
            <div className="md:flex md:-mx-4 mb-4">
                <div className="md:w-1/2 md:px-4 mb-4 md:mb-0">
                    <div className="px-2 py-6 bg-white shadow text-lights-bluebold font-sans font-semibold text-14 flex items-center md:px-6">
                        {total !== null ? <IncomeIcon size="large"/> : <Skeleton circle={true} width={40} height={40}/>}
                        <span className="pl-6">
                            {total !== null ? <div className="text-11 font-sans capitalize mb-2">
                                <FormattedMessage id="db.db.income" defaultMessage="Total Income"/>
                            </div>: <div className="mb-1"><Skeleton width={90} height={11}/></div>}
                            {total !== null ? <div className="text-14 font-sans capitalize font-semibold mb-1">
                                {Number(total).toFixed(2)}
                            </div>:<div className=""><Skeleton width={50} height={14}/></div>}
                            {total !== null ? <div className="text-11 text-lights-orangebold font-sans">
                                <Link to="/db/stats/">
                                    <FormattedMessage id="db.db.linkhis" defaultMessage="View transactions"/>
                                </Link>
                            </div>: <div><Skeleton width={100} height={11}/></div>}
                        </span>
                    </div>
                </div>
                <div className="md:w-1/2 md:px-4">
                    <div className="px-2 py-6 bg-white shadow text-lights-bluebold font-sans font-semibold text-14 flex items-center md:px-6">
                        {userInfo ? <ReferralIcon size="large"/> : <Skeleton width={40} height={40} circle/>}
                        <span className="pl-6">
                            {userInfo ? <div className="text-11 font-sans capitalize mb-2">
                                <FormattedMessage id="db.db.ttref" defaultMessage="Total Referral"/>
                            </div>: <div className="mb-1"><Skeleton width={90}/></div>}
                            {userInfo ? <div className="text-14 font-sans capitalize font-semibold mb-1">
                                {Number(userInfo.userCounts[0]) + Number(userInfo.userCounts[1]) + Number(userInfo.userCounts[2])}
                            </div>: <div><Skeleton width={50} height={14}/></div>}
                            {userInfo ? <div className="text-11 text-lights-orangebold font-sans">
                                <Link to="/db/referrals/">
                                    <FormattedMessage id="db.db.linkref" defaultMessage="View referral"/>
                                </Link>
                            </div>: <div><Skeleton width={100} height={11}/></div>}
                        </span>
                    </div>
                </div>
            </div>
            {/* block3 */}
            <div className="md:flex md:-mx-2 mb-4 md:flex-wrap">
                {prices.map((val,key)=>{
                    return (
                        <div className="md:w-1/5 md:px-2" key={key}>
                            <div className="shadow bg-white py-4">
                                {lv ? <div className="font-sans text-14 font-semibold text-center text-lights-bluebold uppercase mb-4">Level {key+1}</div>:<div className="text-center mb-4"><Skeleton width={60} height={18}/></div>}
                                <div className="font-sans font-semibold text-11 text-center text-lights-orangebold capitalize">{lv ? lvs[key]*1000 > Date.now() ? 'Active' : 'Not Active': <Skeleton width={40} height={11}/>}</div>
                                <div className="font-sans font-semibold text-11 text-center text-lights-orangebold capitalize mb-4">{lv ? lvs[key] > 0 ? renderDays(lvs[key]) :'No date':<Skeleton width={50} height={11}/>}</div>
                                <div className="font-sans font-bold text-14 text-center text-lights-bluebold mb-4">{lv ? `${val} ETH`:<Skeleton width={70} height={18}/>}</div>
                                <div className="text-center mb-2">
                                    {lv ? (key+1 <= lv ? (<button className="focus:outline-none inline-block px-4 py-1 rounded bg-lights-orangebold text-white font-sans font-semibold text-11 border border-lights-orangebold hover:bg-lights-bluebold hover:border-lights-bluebold" style={{width: '69px'}} onClick={()=>setShowBox({title: 'Extend Level',level: key+1})}>Extend</button>):(key == lv ? (<button className="focus:outline-none inline-block px-4 py-1 rounded text-lights-bluebold font-sans font-semibold text-11 border border-lights-bluebold hover:bg-lights-bluebold hover:text-white" style={{width: '69px'}} onClick={()=>setShowBox({title: 'Buy Level',level: key+1})}>Buy</button>):(<button className="focus:outline-none inline-block px-4 py-1 rounded text-lights-grayblack font-sans font-semibold text-11 border border-lights-grayblack cursor-not-allowed" style={{width: '69px'}}>Buy</button>))):<Skeleton width={60} height={17}/>}
                                </div>
                                <div className="font-sans text-11 text-lights-gray text-center mb-4">{lv ? '+180 days':<Skeleton width={56} height={14}/>}</div>
                                {lv ? <div className="py-2 bg-lights-grayblack font-sans text-11 text-center mb-px">
                                    <div className="" style={{color: '#666666',fontSize: '9px'}}>Referrals</div>
                                    <div className="text-lights-bluebold font-semibold text-11">{refs[key]}</div>
                                </div>:<div className="mb-px"><Skeleton height={45}/></div>}
                                {lv ? <div className="py-2 bg-lights-grayblack font-sans text-11 text-center">
                                    <div className="" style={{color: '#666666',fontSize: '9px'}}>Income</div>
                                    <div className="text-lights-bluebold font-semibold text-11">{incomes[key]} ETH</div>
                                </div>:<div><Skeleton height={45}/></div>}
                            </div>
                        </div>
                    )
                })}
            </div>
            {/* block5 */}
            <div className="md:flex md:-mx-2">
                {BonusItems.map((val,key)=>{
                    return <BlockBonusItem key={key} l={key+1} i={val[0]} r={val[1]} c={val[2]} ch={val[3]} p={val[4]} userInfo={userInfo}/>
                })} 
            </div>
        </div>
    )
}
export default connect(mapStateToProps)(Db);